import React, { useState } from "react";
import { Button, Form, Col, Row } from "react-bootstrap";
import DatePicker from "react-datepicker";
import * as Api from "../../api";

function CertificateAddForm({
  portfolioOwnerId,
  setCertificates,
  setIsAdding,
}) {
  //useState title  .
  const [title, setTitle] = useState("");
  //useState description  .
  const [description, setDescription] = useState("");
  //useState whenDate  .
  const [whenDate, setWhenDate] = useState(new Date());

  const handleSubmit = async (e) => {
    e.preventDefault();

    // portfolioOwnerId user_id  .
    const user_id = portfolioOwnerId;
    const when_date = whenDate.toISOString().split("T")[0];

    // "certificate/create"  post.
    await Api.post("certificate/create", {
      user_id,
      title,
      description,
      when_date,
    });

    // "educationlist/id"  get.
    const res = await Api.get("certificatelist", user_id);
    // certificates response data .
    setCertificates(res.data);
    // certificate   , isAdding false .
    setIsAdding(false);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group controlId="certificateAddTitle">
        <Form.Control
          type="text"
          placeholder=" "
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
      </Form.Group>

      <Form.Group controlId="certificateAddDescription" className="mt-3">
        <Form.Control
          type="text"
          placeholder=""
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />
      </Form.Group>

      <Form.Group as={Row} className="mt-3">
        <Col xs="auto">
          <DatePicker
            selected={whenDate}
            onChange={(date) => setWhenDate(date)}
          />
        </Col>
      </Form.Group>

      <Form.Group as={Row} className="mt-3 text-center">
        <Col sm={{ span: 20 }}>
          <Button variant="primary" type="submit" className="me-3">
            
          </Button>
          <Button variant="secondary" onClick={() => setIsAdding(false)}>
            
          </Button>
        </Col>
      </Form.Group>
    </Form>
  );
}

export default CertificateAddForm;
