import React, { useState } from "react";
import { Button, Form, Col, Row } from "react-bootstrap";
import DatePicker from "react-datepicker";
import * as Api from "../../api";

function ProjectAddForm({ portfolioOwnerId, setProjects, setIsAdding }) {
  //useState title  .
  const [title, setTitle] = useState("");
  //useState description  .
  const [description, setDescription] = useState("");
  //useState fromDate  .
  const [fromDate, setFromDate] = useState(new Date());
  //useState toDate  .
  const [toDate, setToDate] = useState(new Date());

  const handleSubmit = async (e) => {
    e.preventDefault();

    // portfolioOwnerId user_id  .
    const user_id = portfolioOwnerId;
    const from_date = fromDate.toISOString().split("T")[0];
    const to_date = toDate.toISOString().split("T")[0];

    // "project/create"  post.
    await Api.post("project/create", {
      user_id,
      title,
      description,
      from_date,
      to_date,
    });

    // "projectlist/id"  get.
    const res = await Api.get("projectlist", user_id);
    // projects response data .
    setProjects(res.data);
    // project   , isAdding false .
    setIsAdding(false);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group controlId="projectAddTitle">
        <Form.Control
          type="text"
          placeholder=" "
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
      </Form.Group>

      <Form.Group controlId="projectAddDescription" className="mt-3">
        <Form.Control
          type="text"
          placeholder=""
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />
      </Form.Group>

      <Form.Group as={Row} className="mt-3">
        <Col xs="auto">
          <DatePicker
            selected={fromDate}
            onChange={(date) => setFromDate(date)}
          />
        </Col>
        <Col xs="auto">
          <DatePicker selected={toDate} onChange={(date) => setToDate(date)} />
        </Col>
      </Form.Group>

      <Form.Group as={Row} className="mt-3 text-center">
        <Col sm={{ span: 20 }}>
          <Button variant="primary" type="submit" className="me-3">
            
          </Button>
          <Button variant="secondary" onClick={() => setIsAdding(false)}>
            
          </Button>
        </Col>
      </Form.Group>
    </Form>
  );
}

export default ProjectAddForm;
