import React, { useState } from "react";
import { Button, Form, Col, Row } from "react-bootstrap";
import * as Api from "../../api";

function EducationEditForm({ currentEducation, setEducations, setIsEditing }) {
  //useState school  .
  const [school, setSchool] = useState(currentEducation.school);
  //useState major  .
  const [major, setMajor] = useState(currentEducation.major);
  //useState position  .
  const [position, setPosition] = useState(currentEducation.position);

  const handleSubmit = async (e) => {
    e.preventDefault();

    // currentEducation user_id user_id  .
    const user_id = currentEducation.user_id;

    // "educations/id"  PUT .
    await Api.put(`educations/${currentEducation.id}`, {
      user_id,
      school,
      major,
      position,
    });

    // "educationlist/id"  GET .
    const res = await Api.get("educationlist", user_id);
    // educations response data .
    setEducations(res.data);
    //   , isEditing false .
    setIsEditing(false);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group controlId="educationEditSchool">
        <Form.Control
          type="text"
          placeholder=" "
          value={school}
          onChange={(e) => setSchool(e.target.value)}
        />
      </Form.Group>

      <Form.Group controlId="educationEditMajor" className="mt-3">
        <Form.Control
          type="text"
          placeholder=""
          value={major}
          onChange={(e) => setMajor(e.target.value)}
        />
      </Form.Group>

      <div key={`inline-radio`} className="mb-3 mt-3">
        <Form.Check
          inline
          label=""
          id="radio-edit-1"
          type="radio"
          name="position"
          value=""
          checked={position === ""}
          onChange={(e) => setPosition(e.target.value)}
        />
        <Form.Check
          inline
          label=""
          id="radio-edit-2"
          type="radio"
          name="position"
          value=""
          checked={position === ""}
          onChange={(e) => setPosition(e.target.value)}
        />
        <Form.Check
          inline
          label=""
          id="radio-edit-3"
          type="radio"
          name="position"
          value=""
          checked={position === ""}
          onChange={(e) => setPosition(e.target.value)}
        />
        <Form.Check
          inline
          label=""
          id="radio-edit-4"
          type="radio"
          name="position"
          value=""
          checked={position === ""}
          onChange={(e) => setPosition(e.target.value)}
        />
      </div>

      <Form.Group as={Row} className="mt-3 text-center mb-4">
        <Col sm={{ span: 20 }}>
          <Button variant="primary" type="submit" className="me-3">
            
          </Button>
          <Button variant="secondary" onClick={() => setIsEditing(false)}>
            
          </Button>
        </Col>
      </Form.Group>
    </Form>
  );
}

export default EducationEditForm;
