import React, { useState } from "react";
import { Button, Form, Col, Row } from "react-bootstrap";
import * as Api from "../../api";

function EducationAddForm({ portfolioOwnerId, setEducations, setIsAdding }) {
  //useState school  .
  const [school, setSchool] = useState("");
  //useState major  .
  const [major, setMajor] = useState("");
  //useState position  .
  const [position, setPosition] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();

    // portfolioOwnerId user_id  .
    const user_id = portfolioOwnerId;

    // "education/create"  post.
    await Api.post("education/create", {
      user_id,
      school,
      major,
      position,
    });

    // "educationlist/id"  get.
    const res = await Api.get("educationlist", user_id);
    // educations response data .
    setEducations(res.data);
    // education   , isAdding false .
    setIsAdding(false);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group controlId="educationAddSchool">
        <Form.Control
          type="text"
          placeholder=" "
          value={school}
          onChange={(e) => setSchool(e.target.value)}
        />
      </Form.Group>

      <Form.Group controlId="educationAddMajor" className="mt-3">
        <Form.Control
          type="text"
          placeholder=""
          value={major}
          onChange={(e) => setMajor(e.target.value)}
        />
      </Form.Group>

      <div key={`inline-radio`} className="mb-3 mt-3">
        <Form.Check
          inline
          label=""
          id="radio-add-1"
          type="radio"
          name="position"
          value=""
          checked={position === ""}
          onChange={(e) => setPosition(e.target.value)}
        />
        <Form.Check
          inline
          label=""
          id="radio-add-2"
          type="radio"
          name="position"
          value=""
          checked={position === ""}
          onChange={(e) => setPosition(e.target.value)}
        />
        <Form.Check
          inline
          label=""
          id="radio-add-3"
          type="radio"
          name="position"
          value=""
          checked={position === ""}
          onChange={(e) => setPosition(e.target.value)}
        />
        <Form.Check
          inline
          label=""
          id="radio-add-4"
          type="radio"
          name="position"
          value=""
          checked={position === ""}
          onChange={(e) => setPosition(e.target.value)}
        />
      </div>

      <Form.Group as={Row} className="mt-3 text-center">
        <Col sm={{ span: 20 }}>
          <Button variant="primary" type="submit" className="me-3">
            
          </Button>
          <Button variant="secondary" onClick={() => setIsAdding(false)}>
            
          </Button>
        </Col>
      </Form.Group>
    </Form>
  );
}

export default EducationAddForm;
