import React, { useState } from "react";
import { Button, Form, Col, Row } from "react-bootstrap";
import DatePicker from "react-datepicker";
import * as Api from "../../api";

function CertificateEditForm({
  currentCertificate,
  setCertificates,
  setIsEditing,
}) {
  //useState title  .
  const [title, setTitle] = useState(currentCertificate.title);
  //useState description  .
  const [description, setDescription] = useState(
    currentCertificate.description
  );
  //useState whenDate  .
  const [whenDate, setWhenDate] = useState(
    new Date(currentCertificate.when_date)
  );

  const handleSubmit = async (e) => {
    e.preventDefault();

    // currentCertificate user_id user_id  .
    const user_id = currentCertificate.user_id;
    const when_date = whenDate.toISOString().split("T")[0];

    // "certificates/id"  PUT .
    await Api.put(`certificates/${currentCertificate.id}`, {
      user_id,
      title,
      description,
      when_date,
    });

    // "certificatelist/id"  GET .
    const res = await Api.get("certificatelist", user_id);
    // certificates response data .
    setCertificates(res.data);
    //   , isEditing false .
    setIsEditing(false);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group controlId="certificateEditTitle">
        <Form.Control
          type="text"
          placeholder=" "
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
      </Form.Group>

      <Form.Group controlId="certificateEditDescription" className="mt-3">
        <Form.Control
          type="text"
          placeholder=""
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />
      </Form.Group>

      <Form.Group as={Row} className="mt-3">
        <Col xs="auto">
          <DatePicker
            selected={whenDate}
            onChange={(date) => setWhenDate(date)}
          />
        </Col>
      </Form.Group>

      <Form.Group as={Row} className="mt-3 text-center mb-4">
        <Col sm={{ span: 20 }}>
          <Button variant="primary" type="submit" className="me-3">
            
          </Button>
          <Button variant="secondary" onClick={() => setIsEditing(false)}>
            
          </Button>
        </Col>
      </Form.Group>
    </Form>
  );
}

export default CertificateEditForm;
