import React, { useState } from "react";
import { Button, Form, Col, Row } from "react-bootstrap";
import DatePicker from "react-datepicker";
import * as Api from "../../api";

function ProjectEditForm({ currentProject, setProjects, setIsEditing }) {
  //useState title  .
  const [title, setTitle] = useState(currentProject.title);
  //useState description  .
  const [description, setDescription] = useState(currentProject.description);
  //useState fromDate  .
  const [fromDate, setFromDate] = useState(new Date(currentProject.from_date));
  //useState toDate  .
  const [toDate, setToDate] = useState(new Date(currentProject.to_date));

  const handleSubmit = async (e) => {
    e.preventDefault();

    // currentProject user_id user_id  .
    const user_id = currentProject.user_id;
    const from_date = fromDate.toISOString().split("T")[0];
    const to_date = toDate.toISOString().split("T")[0];

    // "projects/id"  PUT .
    await Api.put(`projects/${currentProject.id}`, {
      user_id,
      title,
      description,
      from_date,
      to_date,
    });

    // "projectlist/id"  GET .
    const res = await Api.get("projectlist", user_id);
    // projects response data .
    setProjects(res.data);
    //   , isEditing false .
    setIsEditing(false);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group controlId="projectEditTitle">
        <Form.Control
          type="text"
          placeholder=" "
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
      </Form.Group>

      <Form.Group controlId="projectEditDescription" className="mt-3">
        <Form.Control
          type="text"
          placeholder=""
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />
      </Form.Group>

      <Form.Group as={Row} className="mt-3">
        <Col xs="auto">
          <DatePicker
            selected={fromDate}
            onChange={(date) => setFromDate(date)}
          />
        </Col>
        <Col xs="auto">
          <DatePicker selected={toDate} onChange={(date) => setToDate(date)} />
        </Col>
      </Form.Group>

      <Form.Group as={Row} className="mt-3 text-center mb-4">
        <Col sm={{ span: 20 }}>
          <Button variant="primary" type="submit" className="me-3">
            
          </Button>
          <Button variant="secondary" onClick={() => setIsEditing(false)}>
            
          </Button>
        </Col>
      </Form.Group>
    </Form>
  );
}

export default ProjectEditForm;
